.modal{
  position: fixed;
  z-index: 999;
  top:0;
  left: 0;
  width: 100%;
  min-height: 100vh;
 
  display: none;
  justify-content: center;
  align-items: center;
}

.modal.is-open{
  display: flex;
}

.modal-container{
  position: relative;
  width: 300px;

}

.modal-close{
  position: absolute;
  top: 1rem;
  right: 1rem;
}